	module.exports = {"data":{"markdownRemark":{"html":"<center><h2>James DeLay</h2></center>\n<center><h3>Full Stack Software Engineer</h3></center>\n<p>Hello! My name is James and I am a Full Stack Software Engineer from New York.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/PersonalSite/static/Headshot-6dea656b0cc8485d64eb35f5a602a493-a21c5.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 710px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"alt text\"\n        title=\"That's me!\"\n        src=\"/PersonalSite/static/Headshot-6dea656b0cc8485d64eb35f5a602a493-8b432.jpg\"\n        srcset=\"/PersonalSite/static/Headshot-6dea656b0cc8485d64eb35f5a602a493-81942.jpg 178w,\n/PersonalSite/static/Headshot-6dea656b0cc8485d64eb35f5a602a493-60db8.jpg 355w,\n/PersonalSite/static/Headshot-6dea656b0cc8485d64eb35f5a602a493-8b432.jpg 710w,\n/PersonalSite/static/Headshot-6dea656b0cc8485d64eb35f5a602a493-9f4c5.jpg 1065w,\n/PersonalSite/static/Headshot-6dea656b0cc8485d64eb35f5a602a493-a21c5.jpg 1200w\"\n        sizes=\"(max-width: 710px) 100vw, 710px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>While I was pursuing my MBA at Adelphi University I was working as a carpenter. I quickly realized that I loved to build and create things as well as come up with innovative solutions for complex problems. I decided to combine this creative passion with my other passion: technology. This led me to The Fullstack Academy of Code, an immersive coding bootcamp in New York City. It was here where I studied software engineering &#x26; web development. After graduating from the program I was selected to return as a Teaching Fellow where I helped students throughout their journey through the Fullstack curriculum. On a daily basis I would hold office hours, prepare notes and articles for students, and give the occasional lecture.</p>","timeToRead":1,"excerpt":"Hello! My name is James and I am a Full Stack Software Engineer from New York. While I was pursuing my MBA at Adelphi University I was…","frontmatter":{"title":"About Me","cover":"https://get.pxhere.com/photo/abstract-black-and-white-architecture-sky-skyline-photography-view-building-city-skyscraper-new-york-manhattan-cityscape-panorama-downtown-daytime-usa-america-landmark-monochrome-tower-block-sit-background-image-background-legs-outlook-ny-surreal-sw-fantasy-mood-roofs-metropolis-template-photo-montage-expressive-city-panorama-urban-area-monochrome-photography-metropolitan-area-fantasy-picture-1373655.jpg","date":"2018-05-28","category":"tech","tags":null},"fields":{"slug":"/about-me"}},"prev":{"excerpt":"NOTE: This \"post\" is based on  Markdown Cheatsheet  and is meant to test styling of Markdown generated documents…","frontmatter":{"title":"Big Test","cover":null,"date":"2018-01-03"},"fields":{"slug":"/big-sample-test"}},"next":{"excerpt":"NOTE: This \"post\" is based on  Markdown Cheatsheet  and is meant to test styling of Markdown generated documents…","frontmatter":{"title":"Big Test","cover":null,"date":"2018-01-03"},"fields":{"slug":"/big-sample-test"}}},"pathContext":{"slug":"/about-me","total":2,"prev":"/big-sample-test","next":"/big-sample-test"}}

